import { PropsWithChildren } from "react"
import { Box, SxProps, Theme, useTheme } from "@rp/react/web/mui/core"

export const StickedChild = ({ children, sx }: PropsWithChildren<{ sx?: SxProps<Theme> }>) => {
  const theme = useTheme()
  const spread = sx instanceof Function ? sx(theme) : sx
  return (
    <Box
      sx={{
        width: "fit-content",
        position: "sticky",
        marginInline: "auto",
        zIndex: 1,
        ...spread,
      }}
    >
      {children}
    </Box>
  )
}

export const StickedWrapper = ({ children, sx }: PropsWithChildren<{ sx?: SxProps<Theme> }>) => {
  const theme = useTheme()
  const spread = sx instanceof Function ? sx(theme) : sx
  return (
    <Box
      sx={{
        position: "relative",
        display: "flex",
        flexDirection: "column",
        overflow: "clip",
        ...spread,
      }}
    >
      {children}
    </Box>
  )
}
